@import url(../webfonts/stylesheet.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0; }

ul {
  list-style: none;
  padding: 0; }

a:hover {
  text-decoration: none; }

button {
  border: none;
  cursor: pointer;
  outline: none !important; }

input,
textarea {
  outline: none; }

body {
  font-family: "Roboto";
  font-weight: 400; }

.show {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 15px;
  top: 38px;
  vertical-align: middle;
  cursor: pointer;
  border: none;
  background-color: transparent; }
  @media (max-width: 575.98px) {
    .show {
      top: 30px; } }
  .show .fa-eye {
    display: none;
    color: #642887; }
  .show .fa-eye-slash {
    color: #642887; }

.show2 .fa-eye {
  color: #fff; }

.show2 .fa-eye-slash {
  color: #fff; }

.particles {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #fff; }

.error {
  font-size: 12px;
  color: #ea4335;
  margin-bottom: 0; }

.error-border {
  box-sizing: inherit;
  border: 1px solid #ea4335 !important; }

.overlay, .form-panel.auth:before {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%; }

.form-toggle {
  z-index: 80;
  position: absolute;
  top: 40px;
  right: 40px;
  background: #fff;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  transform-origin: center;
  transform: translate(0, -25%) scale(0);
  opacity: 0;
  cursor: pointer;
  transition: all 0.3s ease; }
  @media (max-width: 575.98px) {
    .form-toggle {
      top: 35px;
      right: 15px;
      width: 30px;
      height: 30px; } }
  .form-toggle:before, .form-toggle:after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 4px;
    background: #642887;
    transform: translate(-50%, -50%); }
  .form-toggle:before {
    transform: translate(-50%, -50%) rotate(45deg); }
  .form-toggle:after {
    transform: translate(-50%, -50%) rotate(-45deg); }
  .form-toggle.visible {
    transform: translate(0, -25%) scale(1);
    opacity: 1; }

.header {
  background: #fff; }
  .header-nav {
    position: fixed; }
    .header-nav__logo {
      width: 360px; }
      @media (max-width: 575.98px) {
        .header-nav__logo {
          width: 250px; } }
  .header-form {
    z-index: 99;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    width: 600px;
    height: 612px;
    border-radius: 4px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    overflow: hidden; }
    @media (max-width: 767.98px) {
      .header-form {
        width: 92%; } }
    @media (max-width: 575.98px) {
      .header-form {
        height: 486px; } }
    .header-form input {
      outline: none;
      display: block;
      background: rgba(0, 0, 0, 0.1);
      width: 100%;
      border: 0;
      border-radius: 4px;
      box-sizing: border-box;
      padding: 12px 20px;
      color: rgba(0, 0, 0, 0.6);
      font-family: inherit;
      font-size: inherit;
      font-weight: 500;
      line-height: inherit;
      transition: 0.3s ease; }
      @media (max-width: 575.98px) {
        .header-form input {
          height: 35px; } }
      .header-form input:focus {
        color: rgba(0, 0, 0, 0.8); }
      .reg .header-form input {
        color: #fff; }
        .reg .header-form input:focus {
          color: #fff; }

.form-panel {
  padding: 100px calc(5% + 60px) 60px 60px;
  box-sizing: border-box; }
  @media (max-width: 575.98px) {
    .form-panel {
      padding: 60px calc(5% + 35px) 35px 35px; } }
  .form-panel__header {
    margin: 0 0 40px; }
  .form-panel__title {
    color: #642887;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase; }
    @media (max-width: 575.98px) {
      .form-panel__title {
        font-size: 17px; } }
    .reg .form-panel__title {
      position: relative;
      z-index: 40;
      color: #fff; }
  .form-panel.auth:before {
    content: "";
    display: block;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease; }
  .form-panel.auth.hidden:before {
    display: block;
    opacity: 1;
    visibility: visible; }
  .form-panel.reg {
    z-index: 70;
    position: absolute;
    top: 0;
    left: 95%;
    background: #642887;
    width: 100%;
    min-height: 100%;
    padding: 23px calc(10% + 60px) 60px 60px;
    transition: 0.3s ease;
    cursor: pointer; }
    @media (max-width: 575.98px) {
      .form-panel.reg {
        padding: 35px calc(10% + 35px) 35px 35px; } }
    .form-panel.reg:before, .form-panel.reg:after {
      content: "";
      display: block;
      position: absolute;
      top: 60px;
      left: 1.5%;
      background: rgba(255, 255, 255, 0.2);
      height: 30px;
      width: 2px;
      transition: 0.3s ease; }
    .form-panel.reg:after {
      left: 3%; }
    .form-panel.reg:hover {
      left: 93%;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); }
      .form-panel.reg:hover:before, .form-panel.reg:hover:after {
        opacity: 0; }
    .form-panel.reg.active {
      left: 10%;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
      cursor: default; }
      .form-panel.reg.active:before, .form-panel.reg.active:after {
        opacity: 0; }

.form-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 20px; }
  .form-group #usernameReg,
  .form-group #passwordReg,
  .form-group #passwordConfirm,
  .form-group #email {
    color: #fff; }
  .form-group:last-child {
    margin: 0; }
  .form-group__title {
    display: block;
    margin: 0 0 10px;
    color: rgba(0, 0, 0, 0.6);
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 0.2em; }
    @media (max-width: 575.98px) {
      .form-group__title {
        letter-spacing: 0.1em; } }
    .reg .form-group__title {
      color: #fff; }
  .form-group__btn {
    outline: none;
    background: #642887;
    width: 100%;
    border: 0;
    border-radius: 4px;
    padding: 12px 20px;
    color: #fff;
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    text-transform: uppercase;
    cursor: pointer; }
    @media (max-width: 575.98px) {
      .form-group__btn {
        height: 35px;
        padding: 0;
        font-size: 14px; } }
    .reg .form-group__btn {
      background: #fff;
      color: #642887; }
  .form-group__remember {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0;
    text-transform: none; }
    @media (max-width: 575.98px) {
      .form-group__remember {
        margin-bottom: 0; } }
    .form-group__remember input[type="checkbox"] {
      display: inline-block;
      width: auto;
      margin: 0 10px 0 0; }
  .form-group__recovery {
    color: rgba(0, 0, 0, 0.6);
    font-size: 12px;
    text-decoration: none;
    transition: 0.5s; }
    .form-group__recovery:hover {
      color: #642887; }
    @media (max-width: 575.98px) {
      .form-group__recovery {
        line-height: 32px; } }

.footer {
  text-align: center;
  padding: 25px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #642887;
  color: #fff; }
  @media (max-width: 575.98px) {
    .footer {
      padding: 17px 0; } }
  .footer-text {
    font-size: 14px; }
    @media (max-width: 575.98px) {
      .footer-text {
        font-size: 11px; } }
